<template>
    <div>
        <Title>
            <h4>好书推荐</h4>
        </Title>
        <div class="bookList">
            <div class="mod-bookList">

                <div class="listBox" v-for="(item,index) in goodBookList">
                    <router-link :to="{name:'detailsPage',params: {id:item.id,bookPath:item.path} }">
                        <div class="mod-book">
                            <div class="bookImg">
                                <img :src="item.img" alt="" width="100%" height="100%">
                            </div>
                            <p class="bookTitle">{{item.title}}</p>
                            <span class="count">{{item.count}}万次</span>
                        </div>
                    </router-link>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    import Title from './Title.vue'
    export default {
        name: "Recommended",
        components: {
            Title
        },
        data() {
            return {
                goodBookList: {}
            }
        },
        mounted() {
            this.getData()
        },
        methods: {
            async getData() {
                this.$axios.get('./shelfImg/shelfData.json').then(res => {
                    this.goodBookList = res.data.goodBookList
                })
            }
        }
    }
</script>

<style lang='scss' scoped>
    * {
        padding: 0;
        margin: 0;
    }


    a {
        color: black;
    }

    .listBox {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 27.5vw;
        margin-right: 4vw;
        height: 26vh;
    }

    .listBox:last-of-type {
        margin-right: 0;
    }

    .bookList {
        overflow-x: scroll;
    }

    .bookList::-webkit-scrollbar {
        width: 0 !important
    }

    .mod-bookList {
        display: flex;
        width: 200%;
    }



    .bookImg {
        width: 100%;
        height: 16vh;
    }

    .bookTitle {
        width: 92%;
        font-size: 0.95rem;
        font-weight: bold;
        margin: 6px auto;
    }

    .count {
        position: absolute;
        bottom: 0px;
        left: 0;
        font-size: 0.75rem;
        color: #9f9f9f;
    }
</style>