<template>
    <div class="playListBox" @click="play">
        <div class="sort">
            <span class="sortNum">{{serialNumber}}</span>
        </div>
        <div class="info">
            <div style="width:80%">
                <p class="bookTitle">{{dataList.title}}·{{dataList.directoryName}}</p>
                <div class="infoBotton">
                    <span class="duration">时长 {{dataList.duration}}</span>
                    <span class="duration">{{dataList.date}}</span>
                </div>
            </div>
            <div class="infoRight">
                <span>
                    <van-icon name="down" />
                </span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "playList",
        props: ['dataList', 'serialNumber'],
        data() {
            return {

            }
        },
        methods: {
            play() {
                let title = this.dataList.title + '·' + this.dataList.directoryName
                this.$bus.$emit('playInfo', title, this.serialNumber)
            }
        }
    }
</script>

<style lang='scss' scoped>
    * {
        padding: 0;
        margin: 0;
    }

    .playListBox {
        display: flex;
        align-items: center;

    }

    .sort {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 2rem;
        height: 2rem;
    }

    .sortNum {
        color: #747474;
        font-size: 1.3rem;
    }

    .bookTitle {
        font-size: 1rem;
        font-weight: 500;
    }

    .info {
        display: flex;
        flex: 1;
        align-items: center;
        margin-left: 1rem;
        padding: 1rem 0;
        border-bottom: 1px solid #ccc;
    }

    .infoBotton {
        width: 100%;
        margin-top: 0.3rem;
    }

    .duration {
        font-size: 0.75rem;
        color: #747474;
        margin-right: 2rem;
    }

    .infoRight {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
    }
</style>