<template>
    <div class="playBox">
        <div class="bookImg">
            <img :src="bookImg" alt="" width="100%" height="100%">
        </div>
        <div class="playBoxCenter">
            <p class="bookTitle">{{data}}</p>
            <span class="type">正在播放第{{serialNumber}}集</span>
        </div>
        <div class="playBoxRight">
            <span>
                <van-icon @click="flag=!flag" :name="flag ? 'pause-circle-o' : 'play-circle-o'" size="35"
                    color="#fff" />
            </span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "playBox",
        props: ['bookImg'],
        data() {
            return {
                data: '',
                serialNumber: '',
                flag: false

            }
        },
        mounted() {
            // console.log('School',this)
            this.$bus.$on('playInfo', (data, serialNumber) => {
                this.data = data
                this.serialNumber = serialNumber

            })
        },
        beforeDestroy() {
            this.$bus.$off('playInfo')
        }
    }
</script>

<style lang='scss' scoped>
    .playBox {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        background-color: rgba(0, 0, 0, .2);
        margin: 1rem 0;
        padding: 0.75rem 0;
        border-radius: 10px;
    }

    .bookImg {
        position: relative;
        width: 12vw;
        height: 16vw;

        img {
            position: relative;
            z-index: 9;
        }
    }

    .bookImg::before {
        content: '';
        position: absolute;
        left: 1rem;
        top: 0px;
        width: 16vw;
        height: 16vw;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, .1);
    }

    .bookImg::after {
        content: '';
        position: absolute;
        left: 2rem;
        top: 15px;
        width: 8vw;
        height: 8vw;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, .1);
    }

    .playBoxCenter {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 60%;
    }

    .bookTitle {
        color: #fff;
        font-weight: bold;
    }

    .type {
        color: #B2C1C2;
        margin-top: 4px;
    }

    .playBoxRight {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 12vw;
        height: 16vw;
    }

    .icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        background: url(https://imgbed.link/file/1964);
    }
</style>