<template>
    <div class="searchList" v-show="msg">
        <ul>
            <template v-for="(item,index) in searchList">
                <li v-if="index < 6">
                    <div class="searchContent" @click="addSearchDate(item.name)">
                        <a href="javascript:;" v-html="brightenKeyword(item.name, msg)">{{item.name}}</a>
                    </div>
                </li>
            </template>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "searchList",
        props: ['msg'],
        mounted() {
            this.$bus.$on('getsearchList', (data) => {
                this.searchList = data
            })
        },
        methods: {
            brightenKeyword(val, keyword) {
                const Reg = new RegExp([keyword], 'g');
                if (val) {
                    const res = val.replace(Reg, `<span style="color: #3aa1ff;">${keyword}</span>`);
                    // console.log(res); 
                    return res;
                }
            },
            addSearchDate(data) {
                let searchArr = [localStorage.getItem('searchHistory')]
                searchArr.unshift(data)
                localStorage.setItem('searchHistory', searchArr)

                this.$axios.get('/api/searchdzh.m?word=' + data + '&page_id=1&count=20&cid=eef_&os=ios&appverion=1049').then(res => {
                    this.$router.replace({
                        name: 'SrarchList',
                        query: {
                            SrarchList: res.data.all_book_items
                        }
                    })
                })


                console.log(searchArr)
            }
        },
        data() {
            return {
                searchList: undefined,
            }
        }
    }
</script>

<style lang='scss' scoped>
    a {
        font-size: 1rem;
        color: black;
    }

    .searchList {
        position: absolute;
        top: 57px;
        left: 56px;
        z-index: 99;
        background-color: #eee;
        width: 70%;
        border-radius: 1rem;
    }

    .searchContent {
        display: flex;
        align-items: center;
        height: 6vh;
        padding: 0 1rem;
    }
</style>