<template>
    <div class="detailsPage">
        <detailsPageTop></detailsPageTop>
        <template v-for="(item,index) in bookList">
            <div v-if=" item.id == id">
                <div class="detailsPageBox">
                    <bookCove :BookInfo="item"></bookCove>
                </div>

                <div class="bookContent">
                    <bookInfo :bookInfo="item"></bookInfo>
                    <div class="mod-bookContent">
                        <TitleVue>
                            <h4>简介</h4>
                            <div slot="more">
                            </div>
                        </TitleVue>
                        <p class="abstract">
                            {{item.content}}
                        </p>
                        <TitleVue>
                            <h4>目录</h4>
                            <div slot="more" class="more">
                                <span class="icon"></span>
                                <span class="blues">共{{item.blues}}集</span>
                                <span>
                                    <van-icon name="arrow-down" size="14" />
                                </span>
                            </div>
                        </TitleVue>
                        <template v-for="(item,index) in directoryList">
                            <playList :dataList="item" :serialNumber="index+1"></playList>
                        </template>
                    </div>
                </div>
            </div>
        </template>

    </div>
</template>

<script>
    import detailsPageTop from './components/detailsPageTop.vue'
    import bookCove from './components/bookCove.vue'
    import bookInfo from './components/bookInfo.vue'
    import TitleVue from '../components/Title.vue'
    import playList from './components/playList.vue'
    export default {
        components: {
            detailsPageTop,
            bookCove,
            bookInfo,
            TitleVue,
            playList
        },
        data() {
            return {
                id: '',
                bookPath: "",
                bookList: {},
                directoryList: []

            }
        },
        mounted() {
            this.id = this.$route.params.id
            this.getData()
            this.getdDrectoryData()
        },
        methods: {
            async getData() {
                this.$axios.get('./shelfImg/shelfData.json').then(res => {
                    this.bookList = res.data[this.$route.params.bookPath]
                })
            }
            ,
            async getdDrectoryData() {
                this.$axios.get('./shelfImg/shelfBookDirectory.json').then(res => {
                    res.data[this.$route.params.bookPath].forEach(element => {
                        if (element.id == this.$route.params.id) {
                            this.directoryList = element.directory
                        }
                    });


                })
            }
        }
    }
</script>

<style lang='scss' scoped>
    .detailsPage {
        padding-bottom: 4.5rem;
        background-color: #6c8c93;
    }

    .detailsPageBox {
        padding: 0 1rem;

    }



    .more {
        display: flex;
        align-items: center;
    }

    .blues {
        font-size: 0.9rem;
        color: #5A5A5A;
        margin: 0 1rem;
    }

    .bookContent {
        width: 100vw;
        background-color: #fff;
        border-radius: 1rem 1rem 0 0;
    }

    .abstract {
        font-size: 0.75rem;

    }

    .mod-bookContent {
        padding: 0 1rem;
    }
</style>