<template>
    <div>
        <div class="searchBox">
            <div>
                <router-link :to="path" active-class="active">
                    <span class="icon">
                        <van-icon name="arrow-left" />
                    </span>
                </router-link>
            </div>
            <div class="searchContent">
                <input type="text" class="input-search" v-model="msg" @keydown="debounce" placeholder="宿主又在崩剧情">
            </div>
            <div>
                <span @click="addSearchList">搜索</span>
            </div>
            <searchList :msg="msg"></searchList>
        </div>
        <TitleVue>
            <h4>历史搜索</h4>
            <span slot="more" @click="clear">
                <van-icon name="delete-o" size="20" />
            </span>
        </TitleVue>
    </div>

</template>

<script>
    import List from './List.vue'
    import TitleVue from '../../components/Title.vue';
    import searchList from './searchList.vue';

    TitleVue
    export default {
        name: "Searchbox",
        props: ['path'],
        components: {
            TitleVue,
            List,
            searchList
        },
        data() {
            return {
                msg: '',
                flag: true,
                timeout: null,
            }
        }, mounted() {

        },
        methods: {
            clear() {
                this.$bus.$emit('getsearchHistory', '')
                localStorage.getItem('searchHistory').split(',').forEach(element => {
                    localStorage.removeItem('searchHistory', element)
                });
            },
            async searchBook() {
                if (this.msg) {
                    this.$axios.get('/api/searchdzh.m?word=' + this.msg + '&page_id=1&count=20&cid=eef_&os=ios&appverion=1049').then(res => {
                        this.$bus.$emit('getsearchList', res.data.all_book_items)
                    })
                }
                // this.$axios.get('/api/cats/lv2/statistics').then(res => {
                //     this.$bus.$emit('getsearchList', res.data.female)
                // })
            },
            // 防抖函数
            debounce() {
                if (this.timeout) {
                    clearTimeout(this.timeout)
                }
                this.timeout = setTimeout(() => {
                    this.searchBook()
                }, 500)
            },
            addSearchList() {
                if (this.msg) {
                    let searchArr = [localStorage.getItem('searchHistory')]
                    searchArr.unshift(this.msg)
                    localStorage.setItem('searchHistory', searchArr)
                    this.$bus.$emit('getsearchHistory', localStorage.getItem('searchHistory'))

                    this.$axios.get('/api/searchdzh.m?word=' + this.msg + '&page_id=1&count=20&cid=eef_&os=ios&appverion=1049').then(res => {
                        this.$router.replace({
                            name: 'SrarchList',
                            query: {
                                SrarchList: res.data.all_book_items
                            }
                        })
                    })

                }

            }
        }

    }
</script>

<style lang='scss' scoped>
    .searchBox {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 20px 0;
    }

    .icon i {
        font-weight: bold;
    }

    .searchContent {
        position: relative;
        width: 80%;
        height: 4vh;
        border-radius: 4vh;
        overflow: hidden;
    }

    .input-search {
        width: 100%;
        height: 100%;
        border: none;
        outline: none;
        font-size: 0.75rem;
        background-color: #f3f3f3;
        padding-left: 1rem;
    }
</style>