<template>
    <div class="search">
        <Searchbox :path="path"></Searchbox>
        <ListVue :list="searchList"></ListVue>
        <TitleVue>
            <h4>推荐好书</h4>
            <span slot="more">
                <van-icon name="replay" size="20" />
            </span>
        </TitleVue>
        <ListVue :list="recommend"></ListVue>

        <TitleVue>
            <h4>精品排行</h4>
            <span slot="more">
            </span>
        </TitleVue>
        <rankingVue :list="list">
            <template v-for="(item,index) in list">
                <span :slot="index" :data-index="index" class="rankingSpan">{{index + 1}}</span>
            </template>
        </rankingVue>
    </div>
</template>

<script>
    import Searchbox from './components/Searchbox.vue'
    import TitleVue from '../components/Title.vue'
    import ListVue from './components/List.vue'
    import rankingVue from './components/ranking.vue'
    export default {
        name: "",
        components: {
            Searchbox,
            TitleVue,
            ListVue,
            rankingVue
        },
        data() {
            return {
                path: 'Shelf',
                searchList: undefined,
                list: ['萌医甜妻', '下堂王妃逆袭记', '梦回唐朝', '最佳上门女婿', '绝代双骄', '南宋第一卧底', '纯黑的噩梦', '女总裁的上门女…', '娇妻高高在上', '都市无敌医圣'],
                recommend: ['玄幻', '玄幻', '重生文', '出版高考密码', '舞蹈', '网页制作', '图形图象 / 视频']
            }
        },
        methods: {
            getSearchData() {
                if (localStorage.getItem('searchHistory')) {
                    this.searchList = localStorage.getItem('searchHistory').split(',')
                }
                this.$bus.$on('getsearchHistory', (data) => {
                    // console.log(data);
                    this.searchList = ''
                    if (data) { this.searchList = data.split(',') }
                })
            }
        },
        mounted() {
            this.getSearchData()
        },
        beforeDestroy() {
            this.$bus.$off('getsearchHistory')
        }
    }
</script>

<style lang='scss' scoped>
    .search {
        padding: 0 1rem;
        padding-bottom: 4.5rem;
    }

    .rankingSpan {
        display: inline-block;
        width: 4vw;
        height: 4vw;
        margin-right: 0.6rem;
        background-color: #d7d7d7;
        text-align: center;
        color: #fff;
        font-size: 0.75rem;
        line-height: 4vw;
        border-radius: 4px;
    }

    .rankingSpan[data-index="0"] {
        background-color: red;
    }

    .rankingSpan[data-index="1"] {
        background-color: #ff9800
    }

    .rankingSpan[data-index="2"] {
        background-color: #ffd502
    }
</style>