<template>

    <div class="searcListBox">
        <div>
            <div class="searchBox">
                <div>
                    <router-link to="/searchPage" active-class="active">
                        <span class="icon">
                            <van-icon name="arrow-left" />
                        </span>
                    </router-link>
                </div>
                <div class="searchContent">
                    <input type="text" class="input-search" v-model="msg" @keydown="debounce" placeholder="宿主又在崩剧情">
                </div>
                <div>
                    <span @click="searchBtn">搜索</span>
                </div>
                <searchList :msg="msg"></searchList>
            </div>
            <list :list="list"></list>
        </div>
    </div>
</template>

<script>
    import list from "./compontents/list"
    import Searchbox from "../searchPage/components/Searchbox"
    import searchList from "../searchPage/components/searchList"
    export default {
        name: "",
        mounted() {
            this.list = this.$route.query.SrarchList
        },
        components: {
            list,
            Searchbox,
            searchList
        },
        methods: {
            async searchBook() {
                if (this.msg) {
                    this.$axios.get('/api/searchdzh.m?word=' + this.msg + '&page_id=1&count=20&cid=eef_&os=ios&appverion=1049').then(res => {
                        this.$bus.$emit('getsearchList', res.data.all_book_items)
                    })
                }
                // this.$axios.get('/api/cats/lv2/statistics').then(res => {
                //     this.$bus.$emit('getsearchList', res.data.female)
                // })
            },
            // 防抖函数
            debounce() {
                if (this.timeout) {
                    clearTimeout(this.timeout)
                }
                this.timeout = setTimeout(() => {
                    this.searchBook()
                }, 500)
            },
            searchBtn() {
                if (this.msg) {
                    this.$axios.get('/api/searchdzh.m?word=' + this.msg + '&page_id=1&count=20&cid=eef_&os=ios&appverion=1049').then(res => {
                        this.list = res.data.all_book_items
                    })
                    this.msg = ''
                }
            }
        },
        data() {
            return {
                list: undefined,
                msg: ''
            }
        }
    }
</script>

<style lang='scss' scoped>
    .searcListBox {
        padding: 0 1rem;
        padding-bottom: 4.5rem;
    }

    .searchBox {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 20px 0;
    }

    .icon i {
        font-weight: bold;
    }

    .searchContent {
        position: relative;
        width: 80%;
        height: 4vh;
        border-radius: 4vh;
        overflow: hidden;
    }

    .input-search {
        width: 100%;
        height: 100%;
        border: none;
        outline: none;
        font-size: 0.75rem;
        background-color: #f3f3f3;
        padding-left: 1rem;
    }
</style>