import Vue from 'vue'
//导入路由组件
import VueRouter from 'vue-router'
//导入四个主界面组件
import Bag from "../views/bag/bag"
import Shelf from "../views/shelf/shelf"
import Social from "../views/social/social"
import My from "../views/my/my"

//导入书单分类
import classification from "../views/shelf/classification/classification"
//导入搜索页面
import Search from "../views/shelf/searchPage/Search"
//导入详情页
import detailsPage from "../views/shelf/detailsPage/detailsPage"

import SrarchList from "../views/shelf/SrarchList/SrarchList"

//使用路由器
Vue.use(VueRouter)

//定义并默认暴露路由器
export default new VueRouter({
  routes: [
    {
      path: "/Bag",
      component: Bag
    },
    {
      path: "/Shelf",
      component: Shelf
    },
    {
      path: "/Social",
      component: Social
    },
    {
      path: "/My",
      component: My
    },
    {
      path: "/classification",
      component: classification
    },
    {
      path: "/searchPage",
      component: Search
    },
    {
      path: "/detailsPage:id",
      name: 'detailsPage',
      component: detailsPage
    },
    {
      path: "/SrarchList",
      name: 'SrarchList',
      component: SrarchList
    }
  ]
})
